import React from 'react'
import Label from '../../../label'
import styles from './label.scss'

import labelBadge from '../../../res/icon/label-badge.png'

import {connect} from 'react-redux'

const LabelDemo = (props) => {
    return (
        <div>
            <div className={styles['label-panel']}>
                <Label.BaseLabel label="" name="392"></Label.BaseLabel>
                <Label.BaseLabel label="" name="190293"></Label.BaseLabel>
                <Label.BaseLabel label="" name="2983"></Label.BaseLabel>
                <Label.BaseLabel label="" name="I" icon={labelBadge} noneBorderBottom></Label.BaseLabel>
            </div>

            <div className={styles['info-panel']}>
                <Label.InfoLabel label="" name="2017000001"></Label.InfoLabel>
                <Label.InfoLabel label=""></Label.InfoLabel>
                <Label.InfoLabel label="6"></Label.InfoLabel>
                <Label.InfoLabel label="" noneBorderBottom></Label.InfoLabel>
            </div>

            <div className={styles['label-panel']}>
                <Label.TextLabel label="" name="2016.3.8  900~1530 "></Label.TextLabel>
                <Label.TextLabel label="" name=""></Label.TextLabel>
            </div>

            <div className={styles['cell-panel']}>
                <Label.CellLabel label="" onClick={(event) => alert('')}></Label.CellLabel>
                <Label.CellLabel label="" name="49.95" noneNext></Label.CellLabel>

                <Label.CellLabel label="" name="" noneMarginBottom
                                 onClick={(event) => alert('')}></Label.CellLabel>
                <Label.CellLabel label="" noneMarginBottom></Label.CellLabel>
                <Label.CellLabel label=""></Label.CellLabel>
            </div>
        </div>
    )
};

export default LabelDemo


