/**
 * action
 * actionreducertype
 * action
 */
export const courseOutingListLoading = () => {
    return {
        type: courseOutingListLoading.name
    }
};

export const courseOutingListOnLoad = (data) => {
    return {
        type: courseOutingListOnLoad.name,
        data: data
    }
};


