import React from 'react'
import Bar from '../../../bar'
import { Li, Api, Tr } from '../icon/iconDemo'
import styles from './barDemo.scss'

const tabConfig = [{name: '423',id: '123' },{name: '424', id: '1234'},
                   {name: '425', id: '1236'},{name: '426', id: '1237'}]

const tabConfig2 = [{name: '423',id: '123' },{name: '424', id: '1234'},
                    {name: '425', id: '1236'},{name: '426', id: '1237'},
                    {name: '427', id: '1236'}]

const slideConfig = [{name: '423',id: '123' },{name: '424', id: '1234'},
                     {name: '425', id: '1236'},{name: '426', id: '1237'},
                     {name: '427', id: '1236'},{name: '428', id: '1237'},
                     {name: '429', id: '1236'},{name: '430', id: '1237'},
                     {name: '51', id: '1236'},{name: '52', id: '1237'},]

const iconConfig = [{name: '', id: 'notbegin', count: 8},{name: '', id: 'ongoing', count: 2},
                    {name: '', id: 'isover', count: 21},{name: '', id: 'evaluate', count: 0}]

const BarDemo = () => 
    <section style={{marginTop: '20px'}}>
        <Li desc='CloverMark ' comp={[
            <Bar.CloverMark height='1rem' point={'3.5'} />,
        ]} set={[
            {param: 'height', example: '1rem', desc: '', nec: 'true'},
            {param: 'point', example: '1.5', desc: '0', nec: 'false'},
            {param: 'margin', example: '.4rem', desc: '.2rem', nec: 'false'},
        ]} /> 
        <Li desc='StarRMark ' comp={[
            <Bar.StarRMark height='1.5rem' point={3.4} margin='.3rem' />,
        ]} set={[
            {param: 'height', example: '1.5rem', desc: '', nec: 'true'},
            {param: 'point', example: '3.4', desc: '0', nec: 'false'},
            {param: 'margin', example: '.4rem', desc: '.2rem', nec: 'false'},
        ]} /> 
        <Li desc='StarTMark ' comp={[
            <Bar.StarTMark height='1.5rem' point={4.6} margin='.4rem' />,
        ]} set={[
            {param: 'height', example: '1.5rem', desc: '', nec: 'true'},
            {param: 'point', example: '4.6', desc: '0', nec: 'false'},
            {param: 'margin', example: '.4rem', desc: '.2rem', nec: 'false'},
        ]} /> 
        <Li desc='MarkClick ' comp={[
            <Bar.MarkClick onClick={(i) => {alert(`${i}`)}} />,
        ]} set={[
            {param: 'style', example: '{{width: "50px"}}', desc: '', nec: 'false'},
            {param: 'className', example: 'user-icon', desc: 'CSS', nec: 'false'},
            {param: 'height', example: '1.5rem', desc: '1.5rem', nec: 'false'},
            {param: 'margin', example: '.2rem', desc: '.2rem', nec: 'false'},
            {param: 'src', example: 'mango.png', desc: '', nec: 'false'},
            {param: 'onClick()', example: '(i) => {alert(`${i}`)', desc: '', nec: 'true'},
        ]} /> 
        <Li desc='TabBar  ()' comp={[
            <Bar.TabBar config={tabConfig} onClick={(i) => {console.log(`${i}`)}} />,
            <Bar.TabBar config={tabConfig2} onClick={(i) => {console.log(`${i}`)}} />
        ]} set={[
            {param: 'config', example: '[{name: `423`, id: `123`}, {name: `424`, id: `456`}]', desc: '', nec: 'true'},
            {param: 'onClick()', example: '(i) => {console.log(`${i}`)', desc: '', nec: 'true'},
        ]} /> 
        <Li desc='TabIcon 2 ()' comp={[
            <Bar.TabIcon config={iconConfig} onClick={(i) => {console.log(`${i}`)}} />
        ]} set={[
            {param: 'config', example: '[{name: ``, id: `notbegin`, count: 8},{name: ``, id: `ongoing`, count: 2}]', 
             desc: '9...0', nec: 'true'},
            {param: 'onClick()', example: '(i) => {console.log(`${i}`)', desc: '', nec: 'true'},
        ]} /> 
        <Li desc='TabSlide  ()' comp={[
            <Bar.TabSlide config={slideConfig} onClick={(i) => {console.log(`${i}`)}} />
        ]} set={[
            {param: 'config', example: '[{name: `423`, id: `123`}, {name: `424`, id: `456`}]', desc: '', nec: 'true'},
            {param: 'onClick()', example: '(i) => {console.log(`${i}`)', desc: '', nec: 'true'},
        ]} /> 
    </section>

export default BarDemo


