/**
 * reducer
 * reducer+1+2
 * reducercourseOutingList,
 * reducertypecourseOutingListOnLoad
 */
const courseOutingList = (state = {
    init: false,
    list: []
}, action) => {
    switch (action.type) {
        case 'courseOutingListLoading':
            return {
                init: false,
                list: []
            };
        case 'courseOutingListOnLoad':
            return {
                init: true,
                list: action.data
            };
        default :
            return state;
    }
};

export {courseOutingList}


