import React from 'react'
import Scroll from '../../../scroll'
import styles from './scrollDemo.scss'

// 
class ItemList extends React.Component {
    constructor(...props) {
        super(...props)
        this.state = {
            num: 50,
            status: false,
        }
        this.loadList = this.loadList.bind(this)
    }

    // 
    componentWillReceiveProps(nextProps) {
        if (nextProps.loadingDIO) this.loadList()
    }

    // 
    loadList() {
        this.setState({
            status: true
        })
        setTimeout(() => {
            this.setState({
                num: this.state.num += 10,
                status: false,
            })    
            this.props.loadedDIO()
        }, 1500)   
    }

    render() {
        let list = []

        for(let i = 1; i < this.state.num; i++) {
            list.push(`${i}`)
        }

        const itemList = list.map((item, index) => {
            return (<p key={index}>{item}</p>)
        })

        return (
            <div className={styles['scroll-box']}>
                {itemList}     
                { this.state.status && <p>...</p> }     
            </div>
        )
    }
    
}

// 
const ScrollBox = Scroll.scrollBottom({
    loading: 'loadingDIO', // 
    loaded: 'loadedDIO', // 
})(ItemList)

// demo
const ScrollDemo = () => 
    <section style={{marginTop: '20px'}}>
        <div className={styles['screen']}>
            <div className={styles['top']}>top</div>
                <ScrollBox />
            <div className={styles['bottom']}>bottom</div>
        </div>
    </section>

export default ScrollDemo


