import React from 'react'
import Scroll from '../../../scroll'
import {Api} from '../input/inputDemo'
import styles from './pullDownDemo.scss'

class PullDownDemo extends React.Component {
    constructor(...props) {
        super(...props);
        this.state = {
            loading: false,
            start: 50,
            list: [],
            isEnd: false,
        };

        this.loadData = this.loadData.bind(this);
    }

    componentDidMount() {
        this.loadData();
    }

    loadData() {
        this.setState({loading: true, start: this.state.start - 10});
        //
        setTimeout(() => {
            let list = [];
            for (let i = this.state.start; i <= 50; i++) {
                list.push((<p className={styles["p"]} key={i}>{`${i}`}</p>))
            }
            this.setState({loading: false, list: list, isEnd: this.state.start <= 0})
        }, 1000);
    }

    render() {
        return (
            <div>
                <div className={styles['screen']}>
                    <Scroll.PullDown loading={this.state.loading} isEnd={this.state.isEnd} onReload={this.loadData}
                                     pullHint="" dropHint="" loadingHint="...">
                        <div>{this.state.list}</div>
                    </Scroll.PullDown>
                </div>
                <Api params={[
                    {param: "loading", desc: "", type: "boolean"},
                    {param: "isEnd", desc: "()", type: "boolean"},
                    {param: "onReload", desc: "", type: "function"},
                    {param: "children", desc: "", type: "dom"},
                    {param: "pullHint", desc: " \"\"", type: "string"},
                    {param: "dropHint", desc: " \"\"", type: "string"},
                    {param: "loadingHint", desc: " \"...\"", type: "string"},
                ]}/>
            </div>
        )
    }
}

export default PullDownDemo


