import React from 'react'

import Input from '../../../input'
import styles from './inputDemo.scss'

const InputDemo = (props) => {

    let searchList = "123123123";
    return (
        <div className={styles["demo"]}>
            <h2></h2>
            <Input.Select value={1} options={[{value: "1", text: ""}, {value: "2", text: ""}]}
                          onChange={(oldVal, newVal) => {
                              console.log(oldVal, newVal)
                          }}/>
            <Input.Select sType="round" value={1} options={[{value: "1", text: ""}, {value: "2", text: ""}]}
                          onChange={(oldVal, newVal) => {
                              console.log(oldVal, newVal)
                          }}/>
            <Input.Select style={{width: "300px"}} sType="simple" value={2}
                          options={[{value: "1", text: ""}, {value: "2", text: ""}]}
                          onChange={(oldVal, newVal) => {
                              console.log(oldVal, newVal)
                          }}/>
            <div></div>
            <Api params={[
                {param: "sType", desc: " round/simple", type: "string"},
                {param: "style", desc: "", type: "object"},
                {param: "className", desc: "css", type: "string"},
                {param: "value", desc: "", type: "string"},
                {param: "onChange", desc: " (oldVal={value,text},newVal={value,text})=>{}", type: "function"},
            ]} />

            <h2></h2>
            <Input.NumAdjust value={0} max={10} min={1} editable={1} onChange={(num) => {
                console.log(num)
            }}/>
            <Api params={[
                {param: "style", desc: "", type: "object"},
                {param: "className", desc: "css", type: "string"},
                {param: "editable", desc: "", type: "boolean"},
                {param: "min", desc: "", type: "number"},
                {param: "max", desc: "", type: "number"},
                {param: "value", desc: "", type: "string"},
                {param: "onChange", desc: " (num)=>{}", type: "function"},
            ]} />

            <h2></h2>
            <Input.TextBox value="" onChange={(val) => {
                console.log(val);
            }}/>
            <Input.TextBox value="" sType="underline" onChange={(val) => {
                console.log(val);
            }}/>
            <Input.TextBox placeholder="" reg={/[^0-3]/g} onChange={(val) => {
                console.log(val);
            }}/>
            <Input.TextBox placeholder="..." inType="name" sType="underline" onChange={(val) => {
                console.log(val);
            }}/>
            <Input.PhoneInput placeholder="" onChange={(val, isPhone) => {
                console.log(val, isPhone);
            }}/>
            <Input.TextBox placeholder="" type="password" onChange={(val) => {
                console.log(val);
            }}/>

            <Api params={[
                {param: "input", desc: "input", type: "object"},
                {param: "type", desc: "inputtype,textarea", type: "string"},
                {param: "inType", desc: " name/number(.)reg", type: "string"},
                {param: "reg", desc: "", type: "regexp"},
                {param: "style", desc: "", type: "object"},
                {param: "className", desc: "css", type: "string"},
                {param: "sType", desc: " underline", type: "string"},
                {param: "fullWidth", desc: "", type: "boolean"},
                {param: "value", desc: "", type: "string"},
                {param: "onChange", desc: " (val)=>{}", type: "function"},
            ]} />

            <h2></h2>
            {/*form""*/}
            <form action="" onSubmit={(e)=>{e.preventDefault()}}>
                <Input.SearchInput value="" list={<div style={{background: "#ccc"}}><span> </span></div>}
                                   onChange={(val) => {}} onSearch={(val) => {alert(",: "+val)}}/>
            </form>
            <Api params={[
                {param: "", desc: "", type: ""},
                {param: "list", desc: "", type: "dom"},
                {param: "onSearch", desc: "\"\"", type: "function"},
            ]} />
        </div>
    )
};

export const Api = (props) => {
    const trList = props.params.map((param,index) => {
        return <Tr key={index} {...param}/>
    });
    return (
        <table className="api-table" style={{width: props.width}}>
            <thead>
            <tr>
                <th></th>
                <th></th>
                <th></th>
            </tr>
            </thead>
            <tbody>{trList}</tbody>
        </table>
    )
};

const Tr = (props) => {
    return (
        <tr>
            <th>{props.param}</th>
            <td>{props.desc}</td>
            <td>{props.type}</td>
        </tr>
    )
};

export default InputDemo



