import React from 'react'
import Icon from '../../../icon'
import styles from './iconDemo.scss'

const IconDemo = () => 
    <section style={{marginTop: '20px'}}>
        <Li desc='UserHead ' comp={[
            <Icon.UserHead style={{width: '3.25rem'}} sex='male' onClick={() => alert(``)} />,
            <Icon.UserHead style={{width: '3.25rem'}} sex='female' onClick={() => alert(``)} />,
            <Icon.UserHead style={{width: '3.25rem'}} name='DIO' btn='show' onRemove={() => alert(``)} />,
        ]} set={[
            {param: 'style', example: '{{width: "50px"}}', desc: '', nec: 'false'},
            {param: 'className', example: 'user-icon', desc: 'CSS', nec: 'false'},
            {param: 'sex', example: 'male female', desc: '', nec: 'false'},
            {param: 'name', example: 'DIO', desc: '4px', nec: 'false'},
            {param: 'btn', example: 'show', desc: '#43905c20px', nec: 'false'},
            {param: 'onClick()', example: '( ) => alert(``)', desc: '', nec: 'false'},
            {param: 'onRemove()', example: '( ) => alert(``)', desc: '', nec: 'false'},
        ]} /> 
        <Li desc='SolidIcon ' comp={[
            <Icon.SolidIcon text='' showIcon='true' color='#0099ff' />,
            <Icon.SolidIcon text='' showIcon='true' color='#92d551' />,
            <Icon.SolidIcon text='' color='#ccc' />,
        ]} set={[
            {param: 'style', example: '{{width: "50px"}}', desc: '', nec: 'false'},
            {param: 'className', example: 'user-icon', desc: 'CSS', nec: 'false'},
            {param: 'text', example: '', desc: '', nec: 'true'},
            {param: 'color', example: '#0099ff', desc: '', nec: 'true'},
            {param: 'showIcon', example: 'true', desc: '', nec: 'false'},
        ]} />
        <Li desc='HollowIcon ' comp={[
            <Icon.HollowIcon text='6-8' color='#0099ff' />,
            <Icon.HollowIcon text='' color='#92d551' />,
            <Icon.HollowIcon text='127' color='#fff' />,
        ]} set={[
            {param: 'style', example: '{{width: "50px"}}', desc: '', nec: 'false'},
            {param: 'className', example: 'user-icon', desc: 'CSS', nec: 'false'},
            {param: 'text', example: '', desc: '', nec: 'true'},
            {param: 'color', example: '#0099ff', desc: '', nec: 'true'},
        ]} />
        <Li desc='EllipseIcon ' comp={[
            <Icon.EllipseIcon text='6' color='#fff' bgColor='#0099ff' />,           
            <Icon.EllipseIcon text='#' color='#fff' bgColor='#92d551' />,
            <Icon.EllipseIcon text='#' color='#aaa' bgColor='#fff' />,
        ]} set={[
            {param: 'style', example: '{{width: "50px"}}', desc: '', nec: 'false'},
            {param: 'className', example: 'user-icon', desc: 'CSS', nec: 'false'},
            {param: 'text', example: '', desc: '', nec: 'true'},
            {param: 'color', example: '#fff', desc: '', nec: 'true'},
            {param: 'bgColor', example: '#0099ff', desc: '', nec: 'true'},
        ]} />
        <Li desc='HllipseIcon ' comp={[
            <Icon.HllipseIcon text='' color='#0099ff' />,           
            <Icon.HllipseIcon text='' color='#ddd' />,
            <Icon.HllipseIcon text='' color='#43905c' />,
        ]} set={[
            {param: 'style', example: '{{width: "50px"}}', desc: '', nec: 'false'},
            {param: 'className', example: 'user-icon', desc: 'CSS', nec: 'false'},
            {param: 'text', example: '', desc: '', nec: 'true'},
            {param: 'color', example: '#fff', desc: '', nec: 'true'},
        ]} />
        <Li desc='StatusIcon ' comp={[
            <Icon.StatusIcon text='' color='#0099ff' status='true' />,
            <Icon.StatusIcon text='' color='#92d551' status='true' />,
            <Icon.StatusIcon text='' color='#ccc' />,
            <Icon.StatusIcon text='' color='red' />,
        ]} set={[
            {param: 'style', example: '{{width: "50px"}}', desc: '', nec: 'false'},
            {param: 'className', example: 'user-icon', desc: 'CSS', nec: 'false'},
            {param: 'text', example: '', desc: '', nec: 'true'},
            {param: 'color', example: '#fff', desc: '', nec: 'true'},
            {param: 'status', example: 'true', desc: 'truefalse', nec: 'false'},
        ]} />
        <Li desc='LesStatus ' comp={[
            <Icon.LesStatus status='complete' />,
            <Icon.LesStatus status='noStart' />,
            <Icon.LesStatus status='overdue' />,
            <Icon.LesStatus status='full' />,
            <Icon.LesStatus status='end' />,
        ]} set={[
            {param: 'style', example: '{{width: "50px"}}', desc: '', nec: 'false'},
            {param: 'className', example: 'user-icon', desc: 'CSS', nec: 'false'},
            {param: 'status', example: 'complete', desc: 'completenoStartoverduefullend', nec: 'true'},
        ]} />
        <Li desc='CornerIcon ' comp={[
            <div style={{width: '150px', height: '200px', backgroundColor: '#ccc', position: 'relative'}}>
                <Icon.CornerIcon text='1'  bgColor='#0099ff' length='2rem' position='l-t' />
            </div>,
            <div style={{width: '150px', height: '200px', backgroundColor: '#ccc', position: 'relative'}}>
                <Icon.CornerIcon text='12' bgColor='#92d551' length='2rem' position='r-t' />
            </div>,
            <div style={{width: '150px', height: '200px', backgroundColor: '#ccc', position: 'relative'}}>
                <Icon.CornerIcon text='12' bgColor='red' length='2rem' position='l-b' />
            </div>,
            <div style={{width: '150px', height: '200px', backgroundColor: '#ccc', position: 'relative'}}>
                <Icon.CornerIcon text='12' bgColor='#43905c' length='2rem' tLength='2' />
            </div>,
        ]} set={[
            {param: 'className', example: 'user-icon', desc: 'CSS', nec: 'false'},
            {param: 'text', example: '12', desc: '', nec: 'false'},
            {param: 'bgColor', example: '#0099ff', desc: '', nec: 'true'},
            {param: 'length', example: '2rem', desc: 'rem', nec: 'true'},
            {param: 'position', example: 'l-b', desc: 'l-t l-b r-t r-b', nec: 'false'},
        ]} />
    </section>

export const Api = props => {
    const trList = props.params && props.params.map((param, index) => <Tr key={index} {...param}/>)
    return (
        <table className='api-table'>
            <thead>
            <tr>
                <th></th>
                <th></th>
                <th></th>
                <th></th>
            </tr>
            </thead>
            <tbody>{trList}</tbody>
        </table>
    )
}

export const Tr = props => 
    <tr>
        <th>{props.param}</th>
        <td>{props.example}</td>
        <td>{props.desc}</td>
        <td>{props.nec}</td>
    </tr>

export const Li = props => {
    const itemList = props.comp.map((item, index) => 
        <span key={index}>{item}</span> 
    )
    return (
        <div className={styles['box']}>
            <p className={styles['desc']}>{props.desc}</p>
            { itemList }
            { props.set && <Api params={props.set} /> }
        </div>
    )
}


export default IconDemo


