import React from 'react'
import {connect} from 'react-redux'
import cnTool from 'classnames/bind'
import Page from '../../../page'
import {courseOutingListLoading, courseOutingListOnLoad} from '../action'

import {get} from 'pwfe-dom/net'
import Button from '../../../button'

import styles from './pageDemo.scss'
const cn = cnTool.bind(styles);

class InnerComponent1 extends React.Component{
    constructor(...props){
        super(...props)
        this.state = {
           text:''
        };
        this.networkHandle = this.networkHandle.bind(this)
    }

    networkHandle(){
        get('/demo/data/welcome.json')
            .suc(res=>this.setState({text:res.data}))
            .err((err, res)=>console.log(err))
            .headers((header=>console.log(header)));
    }

    render(){
        const props = this.props;
        return (
            <div>
                <p>DefPage #F2F2F2</p>
                <Button.BaseBtn className={cn('page-btn')} onClick={props.onAddList}> Redux1 </Button.BaseBtn>
                <Button.BaseBtn className={cn('page-btn')} onClick={props.onCleanList}> Redux2 </Button.BaseBtn>
                <Button.BaseBtn className={cn('page-btn')} onClick={this.networkHandle} sType="green"> ,net </Button.BaseBtn>
                <div>
                    {this.state.text}
                </div>
            </div>
        )
    }
}

//connect
const conn = connect(
    (state, props) => {
        //
        return props;
    },
    (dispatch, props)=> {
        //reduxstore
        return {
            onAddList: ()=> {
                let list = [];
                for (let i = 0; i < 51; i++) {
                    list.push(`${i}`)
                }
                dispatch(courseOutingListOnLoad(list));
            },
            onCleanList: ()=> {
                dispatch(courseOutingListLoading())
            }
        };
    });

//(conn)
const InnerComponent1Wrap = conn(InnerComponent1);

/**
 * InnerComponent2
 */
const InnerComponent2 = connect(
    (state, props) => {
        //
        const outingList = state.courseOutingList;
        return { //props
            init: outingList.init,
            list: outingList.list
        };
    }
)(props => {
    const list = props.list;
    const Items = props.init ? list.map((item, index)=>(<p key={index}>{item}</p>)) : (<div>redux</div>);
    return (
        <div>
            <p>ImgPage</p>
            <p></p>
            <br/>
            {Items}
        </div>
    )
});

const PageDemo = props => {
    const {match, location, history, staticContext} = props;//history
    return (
        <div className={cn('page-demo')}>
            <div className={cn('page-box')}>
                <Page.DefPage>{/**/}
                    <InnerComponent1Wrap />
                </Page.DefPage>
            </div>
            <div className={cn('page-box')}>
                <Page.ImgPage>{/**/}
                    <InnerComponent2 />
                </Page.ImgPage>
            </div>
        </div>
    )
};

export default PageDemo



